#container {
	width: 1500px;
	margin-left: auto;
	margin-right: auto;
	padding: 10px;
}

#title {
	text-align: center;
	padding-top: 2px;
	padding-bottom: 2px;
}

#table {
	text-align: center;
	width: 1000px;
	margin-left: auto;
	margin-right: auto;
}

td,
th {
	padding: 8px;
}

#filter {
	width: 1000px;
	margin-right: auto;
	margin-left: auto;
}

#form {
	width: 1000px;
	margin-left: auto;
	margin-right: auto;
}

#form input {
	width: 100%;
	height: 20px;
	margin-top: 5px;
	padding: 10px;
	border: 1px solid #cdcdcd;
	border-radius: 10px;
}

button {
	width: 80px;
	height: 30px;
	background-color: #0366ee;
	color: #fff;
	border-color: #0366ee;
	border-radius: 5px;
	font-size: medium;
}
body {
	font-family: Arial, Helvetica, sans-serif;
}
